<template>
  <div class="d3box">
    <svg id="a2"></svg>
    
  </div>
</template>
<script>
import dat from "@/assets/dat.js";
export default {
  name: "demo",
  data() {
    return {
      aa: dat.data,
      setval: null,
    };
  },
  mounted() {
    console.log('%cdata--length', 'color:green',this.aa.length)
    this.d3draw(this.getArrRandomly(this.aa));
    this.setval = setInterval(() => {
      this.d3draw(this.getArrRandomly(this.aa));
    }, 1000);
  },
  beforeDestroy() {
    window.clearInterval(this.setval);
    this.setval = null;
  },
  methods: {
    getArrRandomly(arr) {
      var len = arr.length;
      //
      for (var i = len - 1; i >= 0; i--) {
        //randomIndex0-arr.length
        var randomIndex = Math.floor(Math.random() * (i + 1));
        //
        var itemIndex = arr[randomIndex];
        arr[randomIndex] = arr[i];
        arr[i] = itemIndex;
      }
      //len-1,len-2,len-3......0
      return arr;
    },
    d3draw(val) {
      if (!val) return;
      var height = 450,
        width = height / 1.138,
        top_margin = ~~(width / 13);

      var svg = d3
        .select("#a2")
        svg.selectAll("g").remove(); //
        svg.attr("width", width)
        .attr("height", height)
        .append("g")
        .attr("transform", `translate(${0},${0})`);

      var data = val;
      const x = d3.scale
        .linear()
        .domain([0, data[0].length - 1])
        .range([0, width]);
      const y = d3.scale
        .linear()
        .domain([0, data.length - 1])
        .range([top_margin, height]);

      var line = d3.svg
        .line()
        .x((d, i) => x(i))
        .y((d) => -d * 0.8);
      /*  */

      svg
        .append("g")
        .selectAll(".wave")
        .data(data)
        .enter()
        .append("path")
        .attr("transform", (d, i) => `translate(0,${y(i * 0.6)})`)
        .attr("class", "wave")
        .attr("d", line);
      /*  */
    },
  },
};
</script>
<style lang="scss">
 svg {
    /*   background-color: rgb(230, 224, 224); */
    .wave {
      fill: white;
      stroke: #444;
      stroke-width: 1;
    }
    // .wave:hover {
    //   fill: rgb(207, 38, 38);
    //   stroke: rgb(207, 38, 38);
    //   stroke-width: 1;
    // }
  }
</style>



